main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}
:root {
  --primary: #024BFB;
  --secondary: #FFBF02;
  --tertiary: #b12704;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
  font-family: Roboto;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  background: var(--dark);
  color: var(--light);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  color: var(--secondary);
}

span:hover {
  color: var(--secondary);
}

header {
  background: var(--dark);
}

header a {
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  background-color: var(--secondary)
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

img {
  max-width: 100%;
}

span[role='img'] {
  cursor: default;
}

form {
  width: 30%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
  cursor: pointer;
}

nav li {
  cursor: pointer;
}

textarea {
  height: 120px;
  border: 3px solid #cccccc;
  padding: 5px;
  font-size: 1rem;
  padding: 0.25rem;
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

.box {
  box-shadow: 0 0 10px #333;
}

.shrink {
  height: auto;
  width: 75%;
}

.technology {
  font-size: small;
}

.text-center {
  text-align: center;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex {
  display: flex;
}

.flex-row {
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
}

.flex-row-nowrap {
  display: flex;
  flex-flow: row;
  flex-wrap: nowrap;
}

.flex-column {
  display: flex;
  flex-flow: column;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.center {
  justify-content: center;
  align-items: center;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 250px;
  margin-bottom: 50px;
  cursor: pointer;
}

.modalBackdrop {
  background: rgba(0, 0, 0, 0.65);
  bottom: 0;
  left: 0;
  overflow: auto;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1;
}

.modalContainer {
  background: var(--darker);
  border-radius: 5px;
  max-width: 100%;
  margin: 50px auto;
  padding: 15px;
  width: 960px;
}

.modalTitle {
  font-size: 25px;
  font-weight: 600;
  margin-bottom: 25px;
  font-weight: bolder;
}

button {
  background: var(--primary);
  border: 0;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 30px;
  padding: 10px 15px;
}

a {
  border-radius: 5px;
  color: var(--primary);
  font-weight: 600;
  padding: 2px 4px;
  margin-left: -2px;
  margin-right: -2px;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.header {
  background-color: var(--dark);
  width: 100%;
}

.footer {
  background-color: var(--dark);
  position: fixed;
  width: 100%;
  bottom:0px;
  padding: 10px;
}

.icons {
  font-size: 35px;
}

.error-test {
  color: var(--error);
  font-size: 10px;
}

.text {
  font-size: 20px;
  width: 50%
}

.navActive {
  color: var(--secondary);
}

@media screen and (max-width: 1280px) {
  .flex-row-nowrap {
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
  }

  img {
    max-width: 50%;
  }

  main {
    margin: 0 auto;
    max-width: 100%;
    padding: 50px 30px;
  }

  .text {
    font-size: 15px;
    width: 75%
  }

  .img-thumbnail {
    width: auto;
    height: auto;
    margin-bottom: 50px;
    cursor: pointer;
  }
  
}

@media screen and (max-width: 700px) {
  .flex-row {
    display: flex;
    flex-flow: column;
    flex-wrap: wrap;
  }
}